<template>
  <div class="animated fadeIn">
    <b-row>
      <b-button class="menu-btn" variant="primary" @click="displayAddShop = !displayAddShop">
        <i class="icon-arrow-up icons font-2xl d-block mt-4 displayAddShopIcon" v-if="displayAddShop"></i>
        <i class="icon-arrow-down icons font-2xl d-block mt-4 displayAddShopIcon" v-else></i>
        添加店铺
      </b-button>
    </b-row>
    <b-card v-show="displayAddShop">
      <div slot="header">
        <strong>添加</strong> 店铺
      </div>
      <b-form>
        <b-form-group validated
                      label="店铺名称"
                      description="请给店铺起个名字">
          <b-form-input id="shopName" type="text" v-model="addShopData.name"></b-form-input>
        </b-form-group>
        <b-form-group validated
                      label="店铺简介"
                      description="店铺简介会显示在店铺首页">
          <b-form-input id="shopNotice" type="text" v-model="addShopData.notice"></b-form-input>
        </b-form-group>
        <div slot="footer">
          <b-button type="button" size="sm" variant="primary" @click="addShop"><i class="fa fa-dot-circle-o"></i> Submit</b-button>
          &nbsp;
          <b-button type="button" size="sm" variant="danger" @click="displayAddShop = !displayAddShop"><i class="fa fa-ban"></i> Reset</b-button>
        </div>
      </b-form>
    </b-card>
    <b-row>
      <b-col>
        <table border="0" class="cTable">
          <tr class="table-line1">
            <th>店铺id</th>
            <th>店铺名称</th>
            <th>店铺简介</th>
            <th>操作</th>
          </tr>
          <tr v-for="(v,k) in list">
            <td>{{v.id}}</td>
            <td>{{v.name}} <i class="icon-note icons font-2xl d-block mt-4" @click="editShop(v.id,'name')"></i></td>
            <td>{{v.notice}} <i class="icon-note icons font-2xl d-block mt-4" @click="editShop(v.id,'notice')"></i></td>
            <td>
              <b-button class="td-btn" variant="primary" @click="delShop(v)">删除店铺</b-button>
            </td>
          </tr>
        </table>
      </b-col>
    </b-row>

  </div>

</template>

<script>
    import normalBtn from './normalBtn.vue';

    export default {
        name: 'tables',
        components: {normalBtn},
        data: () => {
            return {
                list: [],
                displayAddShop: false,
                addShopData: {
                    name: '',
                    notice: ''
                }
            }
        },

        methods: {
            changeValue(key,value){
                this[key] = value;
            },

            listShop() {
                this.$ajax('shop', {
                    method: 'listShop',
                    token: window.localStorage.getItem('token')
                }, (res) => {
                    console.log(res);
                    if (res.code == 0) {
                        this.list = [];
                        for (var i in res.data) {
                            this.list.push(res.data[i]);
                        }
                    } else {
                        alert(res.msg);
                    }
                });
            },

            addShop() {
                console.log(this.addShopData);
                if(confirm("确定要添加\n\n店铺名称："+this.addShopData.name+"\n店铺简介："+this.addShopData.notice+"\n\n吗？")) {
                    this.$ajax('shop',{
                        method: 'addShop',
                        name: this.addShopData.name,
                        notice: this.addShopData.notice,
                        token: window.localStorage.getItem('token')
                    },(res) => {
                        console.log(res);
                        if (res.code == 0) {
                            alert('添加成功');
                            this.listShop();
                        } else {
                            alert(res.msg);
                        }
                    });
                }
            },

            delShop(v) {
                if (confirm("确定要删除以下店铺吗？\n\n" + v.name)) {
                    this.$ajax('shop',{
                        method:'delShop',
                        token: window.localStorage.getItem('token'),
                        shopId: v.id
                    }, (res) => {
                        if (res.code == 0) {
                            alert('删除成功');
                            this.listShop();
                        } else {
                            alert(res.msg);
                        }
                    })
                }
            },

            editShop(id, key) {
                var value = prompt('请输入新的' + (function (key) {
                    switch (key) {
                        case 'name':
                            return '店铺名称';
                        case 'notice':
                            return '店铺简介';
                    }
                })(key));

                if (!value) {
                    return;
                }

                if (confirm('确定要修改吗？')) {
                    this.$ajax('shop', {
                        method: 'editShopInfo',
                        token: window.localStorage.getItem('token'),
                        key: key,
                        value: value,
                        shopId: id
                    }, (res) => {
                        if (res.code == 0) {
                            this.listShop();
                        } else {
                            alert(res.msg);
                        }
                    });
                }
            }
        },

        created() {
            this.listShop();
        }
    }
</script>
<style type="text/css">
  .cTable {
    width: 100%;
  }

  .cTable * {
    text-align: center;
  }

  .table-line1 {
    border-bottom: solid 2px black;
  }

  .td-btn {
    margin: 10px 10px;
  }

  .menu-btn {
    margin: 10px 0 20px 0;
  }

  .icons {
    display: inline !important;
    font-size: 12px !important;
    margin: 0 5px;
    color: blue;
  }

  .displayAddShopIcon {
    color: white;
  }
</style>